import React, { Component } from 'react';
import './scss/Portfolio.scss';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import gravity from './img/gravity.png';
import glint from './img/glint.png';

import { motion } from 'framer-motion';

export default class Portfolio extends Component {
    render () {
        const settings = {
            dots: true,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            appendDots: dots => (
                <div
                style={{
                    backgroundColor: "#1F1F1F",
                    borderRadius: "10px",
                    padding: "10px",
                    color: "white",
                }}
                >
                <ul style={{ margin: "0px" }}> {dots} </ul>
                </div>
            ),
        };

        return (
            <motion.div className="portfolio" 
                exit={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                initial={{ opacity: 0 }}
            >
                <Slider className='slider' {...settings}>
                    <div>
                        <a href="https://jamshoode.github.io/gravity"><img src={gravity} alt="Gravity web site" /></a>  
                    </div>
                    <div>
                        <a href="https://jamshoode.github.io/glint"><img src={glint} alt="Glint web site" /></a>  
                    </div>
                </Slider> 
            </motion.div>
        );
    }
    
}